
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
components: {
XHeader, Group, XInput, XButton, Selector, Datetime
},
data() {
return {
showSearchEnterprise: false,
options: {
sex:[
{key: 0, value: ''},
{key: 1, value: ''}
]
},
form: {
name: '', mobile: '', birthday: '',sex:''
}
}
},
methods: {
judeg() {
if(this.form.name.trim() != ''&& this.form.mobile.trim() != ''&&this.form.birthday.trim() != ''&&this.form.sex != ''){
this.register();
}else{
this.$vux.alert.show({
content:''
})
}
},
register() {
axios.post('api/user/registerWechat', this.form).then(response => {
this.$vux.alert.show({
content:response.data.message
})
}).catch(error => {

})
}
}
}



